import React, { useState } from "react";
import PhoneInput from "react-phone-input-2";
import OrangeButton from "../OrangeButton/OrangeButton";
import styles from "./ApplicationForArch.module.css";

const ApplicationForArch: React.FC = () => {
   const {
      ApplicationForArch,
      right,
      left,
      img,
      title,
      description,
      contactsWrapper,
      wrapper,
      optionPhone,
      phoneContainer,
      inputPhone,
      inputName,
   } = styles;
   const [value, setValue] = useState("");
   return (
      <section id={ApplicationForArch}>
         <div className="container">
            <div className={wrapper}>
               <div className={left}>
                  <img
                     className={img}
                     src="https://arh.yamaster.ml/images/blok9/75-14.png"
                     alt="architechtor"
                     data-aos="zoom-in"
                     data-aos-duration="1500"
                  />
               </div>
               <div
                  data-aos="fade-left"
                  data-aos-duration="1500"
                  className={right}
               >
                  <p className={title}>  </p>
                  <p className={description}>
                        ,  
                     ,   15-   .
                          
                     . <br />
                     <br />
                      2006      
                         ,  
                     :
                     <br />
                     - ;
                     <br />
                     -    ;
                     <br />
                     -  ;
                     <br />
                     -  .
                     <br />
                     <br />
                      -   ,  
                          
                     .
                     <br />
                     <br />
                          ,  
                        ,   , 
                      ,  .
                  </p>
                  <div className={contactsWrapper}>
                     <input
                        className={inputName}
                        type="text"
                        placeholder=" "
                     />
                     <PhoneInput
                        inputClass={inputPhone}
                        country={"ru"}
                        value={value}
                        onChange={setValue}
                        placeholder="  "
                        buttonClass={optionPhone}
                        containerClass={phoneContainer}
                     />
                     <OrangeButton title={" "} width="100%" />
                  </div>
               </div>
            </div>
         </div>
      </section>
   );
};

export default ApplicationForArch;
