import React, { useState } from "react";
import styles from "./BlockHeaderSection.module.css";
import NumberedCard from "../NumberedCard/NumberedCard";
import PhoneInput from "react-phone-input-2";
import "react-phone-input-2/lib/style.css";

type cardType = {
   img: string;
   title: string;
   id: number;
   description: string;
};

const BlockHeaderSection: React.FC = () => {
   const [cards, setCards] = useState<cardType[]>([
      {
         img: "https://i.ibb.co/RBH9ZB6/author-Supervision.webp",
         title: "  ",
         id: 1,
         description:
            "         .  ,      ,       .      .",
      },
      {
         img: "https://i.ibb.co/xYfcrsd/building-Facades.webp",
         title: " ",
         id: 2,
         description:
            "      .          , ,   ,   .",
      },
      {
         img: "https://i.ibb.co/xYfcrsd/building-Facades.webp",
         title: " ",
         id: 3,
         description:
            "         .     ,      .",
      },
      {
         img: "https://i.ibb.co/RBH9ZB6/author-Supervision.webp",
         title: " ",
         id: 4,
         description:
            "    3D-      ,  ,    .",
      },
      {
         img: "https://i.ibb.co/RBH9ZB6/author-Supervision.webp",
         title: " ",
         id: 5,
         description:
            " ,        ,    15 .       ,           .",
      },
      {
         img: "https://i.ibb.co/xYfcrsd/building-Facades.webp",
         title: "",
         id: 6,
         description:
            "          .         . ,        , ,     ,    .      .",
      },
   ]);
   const [value, setValue] = useState("");
   const {
      blockHeaderSection,
      title,
      cardContainer,
      feedbackContainer,
      inputName,
      inputPhone,
      optionPhone,
      phoneContainer,
   } = styles;
   return (
      <section id={blockHeaderSection}>
         <div className="container">
            <p data-aos="zoom-in" data-aos-duration = "1500" className={title}>
                
            </p>

            <div className={cardContainer}>
               {cards.map((card) => {
                  return (
                     <NumberedCard
                        key={card.id}
                        title={card.title}
                        imgSrc={card.img}
                        description={card.description}
                        id={card.id}
                     />
                  );
               })}
            </div>
            <div data-aos="zoom-in" data-aos-duration = "1500" className={feedbackContainer}>
               <input
                  className={inputName}
                  type="text"
                  placeholder=" "
               />
               <PhoneInput
                  inputClass={inputPhone}
                  country={"ru"}
                  value={value}
                  onChange={setValue}
                  placeholder="  "
                  buttonClass={optionPhone}
                  containerClass={phoneContainer}
               />
               {/* ADD BUTTON HERE */}
            </div>
         </div>
      </section>
   );
};

export default BlockHeaderSection;
